export default {
name : 'Posts',
    async asyncData({ $axios }) {
        const temp = await $axios.$get('https://jsonplaceholder.typicode.com/posts')
        
        return { temp }
        },
            
     data(){
        return{
            temp:[],
            posts:[],
            loading:true
        }
    },
    methods :{
        showPosts(){
            this.posts = this.temp
            this.loading = false
        },

    },
    created(){
        this.showPosts()
    }
}
